.footer {
    background-color: #F5F5FA;
    border-top: 4px solid #E6E6F0;
    padding-top: 29px;
    padding-bottom: 42px;

    &__find-map {
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #24A3FF;
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
    }

    &__info {
        display: flex;
        align-items: center;
    }

    &__adress {

        &_black {
            color:#323264;
            margin-right: 22px;
            font-weight: 500;
        }
    }

    &__contacts-about {
        display: flex;
        display: flex;
        justify-content: space-between;
    }

    &__link {
        display: inline-block;
    }

    &__tel-link {
        color: #46466E;
        font-weight: bold;
        display: flex;
        flex-direction: row-reverse;
        margin-right: 6px;
    }

    &__mail-link {
        font-weight: bold;
        font-size: 16px;
        line-height: 19px;
        color: #24A3FF;
        display: flex;
        flex-direction: row-reverse;
    }

    &__contacts {
        padding: 0px;
        margin-top: 20px;
    }

    &__contacts-about {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-align: right;
        color: #B3B3BA;
    }
}
