.header {
    display: flex;
    justify-content: space-between;

    &__mobile-nav {
        width: 18px;
        height: 15px;
        display: none;
        flex-direction: column;
        justify-content: space-between;

        & div {
            border-bottom: 2px solid #FFFFFF;
        }
    }

    &_marg49 {
        margin-top: 49px;
        margin-bottom: 41px;
    }
    &__info {
        display: flex;
        align-items: center;
    }
    &__contacts {
        display: flex;
        flex-direction: column;
    }
    &__list {
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        font-size: 12px;
        line-height: 14px;
        letter-spacing: 1px;
        color: #FFFFFF;
    }
    &__list li {
        padding: 5px 6px;
    }

    &__list li:hover{
        background: #F0145A;
        border-radius: 8px;
    }
    &_separator::after {
        content: '';
        width: 6px;
        height: 6px;
        background-color: #E6E6F0;
        padding: 0px;
        border-radius: 50%;
        display: inline-flex;
        align-self: center;
        margin-left: 10px;
        margin-right: 10px;
    }

    &__logo-tablet {
        display: none;
    }

    &__text {
        font-size: 16px;
        line-height: 19px; /* убрать, чтобы все было ок */
        color: #FFFFFF;
        display: flex;
    }
    &__tel-link {
        font-size: 16px;
        line-height: 19px;
        text-align: right;
        color: #FFFFFF;
        text-decoration: none;
        display: flex;
    }
}

.nav {
    display: flex;
    justify-content: flex-end;
    margin-top: 19px;
    &__list {
        display: flex;
        width: 404px;
        justify-content: space-between;
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #FFFFFF;

        & a {
            border-bottom:  2px solid transparent;
        }

        & a:hover {
            border-color:#FFFFFF;
            cursor: pointer;
        }

    }
}
