.cooperation {
    &__items {
        display: flex;
        flex-wrap: wrap;
    }

    &__header {
        display: flex;
        justify-content: center;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: 1px;
        text-transform: uppercase;
        color: #323264;
        margin-top: 96.75px;
        margin-bottom: 29px;
    }

    &__item {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 25%;
        height: 120px;
        object-fit: cover;

        &:nth-child(1) {
            background-color: $hillel_color;
        }

        &:nth-child(2) {
            background-color: $wb_color;
        }

        &:nth-child(3) {
            background-color: $tapgerin_color;
        }

        &:nth-child(4) {
            background-color: $dLook_color;
        }

        &:nth-child(5) {
            background-color: $arangear_color;
        }

        &:nth-child(6) {
            background-color: $adtell_color;
        }

        &:nth-child(7) {
            background-color: $clickky_color;
        }

        &:nth-child(8) {
            background-color: $ec_color;
        }

        &:nth-child(9) {
            background-color: $infa_color;
        }

        &:nth-child(10) {
            background-color: $cityPark_color;
        }

        &:nth-child(11) {
            background-color: $art-print_color;
        }

        &:nth-child(12) {
            background-color: $uCard_color;
        }
    }
}
