.team {
    display: flex;
    padding-bottom: 130px;

    &-container {
        padding-right: 0px;
        padding-left: 0px;
        margin-right: 15px;
        margin-left: 15px;
        min-width: 340px;
        max-width: 1322px;
    }

    &__swiper {
        width: 76px;
        margin: 0 auto;

        & span {
            height: 16px;
            width: 16px;
        }
    }

    &__item {
        background-color: #F5F5FA;
        border-radius: 12px;
        display: flex;
    }

    &-wrapper {
        display: flex;
        justify-content: center;
    }

    &__text {
        padding: 12px 20px 0px 40px;
    }

    &_mg-top-50 {
        margin-top:50px;
    }

    &-wrapper__slider {
        width: 76px;
        display: flex;
        justify-content: space-between;
        margin: 0 auto;
        &__circle {
            width: 16px;
            height: 16px;
            background-color: #24A3FF;
            opacity: 0.2;
            border-radius: 50%;
        }

        &__circle:hover {
            background-color: blue;
        }

        &_mg-top-20 {
            margin-top: 20px;
        }

        &_mg-bot-96 {
            margin-bottom: 96px;
        }
    }

    &__img-container {
        width: 413px;
        height: 500px;
        display: contents;
    }

    &__link {
        color: #24A3FF;
        opacity: 1;
    }

    &__header {
        font-weight: bold;
        font-size: 42px;
        line-height: 50px;
    }

    &__founer {
        font-weight: 500;
        font-size: 18px;
        line-height: 21px;
        color: #B3B3BA;
        display: flex;
        flex-direction: column;
        margin-top: 20px;

        &__about {
            font-size: 16px;
            line-height: 22px;
            color: #B3B3BA;
            font-weight: normal;
            margin-top: 5px;
        }
    }

    &__about-lead {
        img {
            display: none;
        }
    }
}

#team__swiper {
    bottom: 90px;
}
