.publications {
    background-color: #F5F5FA;
    padding-top: 33.25px;

    &__img-wrapper {
        max-width: -webkit-fill-available;
    }

    &__mb-40 {
        margin-bottom: 40px;
    }

    &__btn-container {
        display: flex;
        justify-content: center;
        padding: 54px 0px;
    }

    &__btn {
        background-color: #24A3FF;
        box-shadow: 0px 0px 20px rgba(36, 163, 255, 0.5);
        border-radius: 12px;
        border: none;
        font-weight: bold;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        width: 250px;
        height: 50px;
    }

    &__items {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    &__text {
        padding: 20px 20px 10px 20px;

        &__header {
            font-weight: 500;
            font-size: 18px;
            line-height: 21px;
            color: #24A3FF;
            padding-bottom: 10px;
        }
    }

    &__item {
        width: 31.375%;
        background-color: #FFFFFF;
        border-radius: 8px;

        &__data {
            font-weight: bold;
            font-size: 12px;
            line-height: 14px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: #B3B3BA;
            border-bottom: 4px solid #E6E6F0;
            padding-bottom: 20px;
        }

        &__info {
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #000000;
            margin-top: 20px;
        }
    }
}
